
import http from "../http";

export {
    getSystemNotifyMessage,
    requestMessageReaded,
    getNoticeDetail
}

//获取系统通知消息
const getSystemNotifyMessage = (reqData) => {
    return new Promise((resolve, reject) => {
        let reqUrl = 'xwj-property-notice/notice/getAllNotice';
        http.get(reqUrl, reqData, 'baseUrl', false).then(noteData => {
            resolve(noteData);
        }).catch(error => {
            reject(error);
        })
    });
}

//设置消息全部已读
const requestMessageReaded = (reqData) => {
    return new Promise((resolve, reject) => {
        let reqUrl = 'xwj-property-notice/notice/updateIgnore';
        http.post(reqUrl, reqData, false, 'baseUrl').then(noteData => {
            resolve(noteData);
        }).catch(error => {
            reject(error);
        })
    });
}

//获取通知详情
const getNoticeDetail = (reqData) => {
    return new Promise((resolve, reject) => {
        let reqUrl = 'xwj-property-notice/notice/getPropertyNotice';
        http.get(reqUrl, reqData, 'baseUrl', false).then(noteData => {
            resolve(noteData);
        }).catch(error => {
            reject(error);
        })
    });
}