<template>
  <div class="notice-detail-page" :class="{themeL:bindThemeL}">
    <!--<van-nav-bar
      class="nav-bar"
      :title="title"
      :border="false"
      left-arrow
      @click-left="onBack"
    />-->
    <navigation-bar  @onLeftClick="onBack" :pageName="title">
    </navigation-bar>

    <div class="notice-content"  v-show="!isNoNetwork">
      <div class="notice-title">{{noticeDetail.NOTICE_NAME}}</div>
      <div class="notice-time">{{noticeDetail.CREATED_TIME}}</div>
      <div class="contentArea" v-html="noticeDetail.CONTENT"></div>
    </div>
    <div class="no-networking" v-show="isNoNetwork">
      <img :src="noNetworkImg">
      <h3>网络请求失败</h3>
      <p>请检查您的网络</p>
      <div @click="loadNoticeDetail">重新加载</div>
    </div>
  </div>
</template>

<script>

  import {
    getNoticeDetail
  } from "../../services/noticeService";
  import NavigationBar from '@/common/currency/NavigationBar.vue';

  import Vue from 'vue';
  import {Toast} from 'vant';
  Vue.use(Toast);

  // 无网络图片
  import noNetworkImg from '@/assets/images/home/notice/img_net@2x.png'

  export default {
    name: "noticeDetail",
    components: {
      NavigationBar
    },
    mounted () {
      // 获取传参
      let routerParams = this.$route.params;
      if (routerParams != undefined
      && routerParams.noticeReq != undefined) {
          this.reqData = routerParams.noticeReq;
      }
      this.loadNoticeDetail();
    },
    data() {
      return {
        active: 0,
        title: '物业通知',
        reqData:{},
        //通知内容
        noticeDetail: {},
        isNoNetwork: false,

        //本地图片
        noNetworkImg: noNetworkImg,

        /*大字模式*/
        bindThemeL: this.$curTheme==0 ? false : true
      };
    },

    methods: {
      onBack() {
        this.$router.back(-1);
      },
      // 加载通知详情
      loadNoticeDetail() {
         getNoticeDetail(this.reqData)
          .then(noteData => {
            this.isNoNetwork = false;
            this.noticeDetail = noteData;
          })
          .catch(error => {
            this.isNoNetwork = true;
            Toast(error);
          });
      }
    }
  };
</script>

<style scoped lang="scss">
$navHeight: 44px;
  .notice-detail-page {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
  }
  /* 导航 */
  .nav-bar {
    position: relative;
    width: 100%;
    height: $navHeight;
    z-index: 66;
  }
  // .van-nav-bar .van-nav-bar__title {
  //   font-family: PingFangSC-Medium !important;
  //   font-size: 17px !important;
  // }
  .notice-content {
    overflow: auto;
    flex: 1;
    box-sizing: border-box;
    width: 100%
  }

  .notice-title {
    padding: 12px 18px;
    font-size: 20px;
    word-wrap: break-word;
    font-weight: 500;
    height: auto;
    color: #000000;
  }
  .notice-time {
    padding: 0px 18px 12px 18px;
    height: auto;
    font-size: 12px;
    color: #000000;
    word-wrap: break-word;
  }
  .themeL .notice-time {
    font-size: 15px;
  }
  .contentArea {
    font-family: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif;
    font-size: 14px;
    background-color: #fff;
    color: #333;
    padding: 0px 18px 12px 18px;
    text-align: left;
    word-break: break-word;
  }
  .themeL .contentArea {
    font-size: 15px;
  }

  .no-networking {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding-top: 90px;
    font-family: PingFangSC-Regular;
    text-align: center;
    img {
      width: 66px;
      margin-bottom: 20px;
    }
    h3 {
      font-size: 16px;
      color: #888888;
      margin: 10px 0px;
    }
    p {
      font-size: 14px;
      color: #C4C4C4;
      margin: 0 0 3px;
    }
    div,
    div:active {
      background-color: #f8f8f8;
      color: #444;
      position: relative;
      margin: 10px;
      border-radius: 20px;
      border: 1px solid #B5B5B5;
      font-size: 16px;
      height: 40px;
      line-height: 40px;
      width: 125px;
    }
    div:active{
      opacity: 0.5;    //这里重要，就是通过这个透明度来设置
    }
  }
</style>
